<template>
  <div
    class="flex flex-col items-center justify-center min-h-screen bg-black text-white text-center"
  >
    <!-- 404 ÈåØË™§Ë®äÊÅØ -->
    <h1 class="text-6xl font-bold text-red-500 animate-bounce">404</h1>
    <p class="text-2xl mt-4">‰Ω†ÊòØ‰∏çÊòØËµ∞ÈåØË∑Ø‰∫ÜÔºüÈÇÑÊòØ‰Ω†ÁöÑ IQ ÂÉèÊÅêÈæç‰∏ÄÊ®£Ôºü</p>
    <p class="text-lg mt-2">Âø´ÂõûÂéªÔºå‰∏çÁÑ∂ÈÄôÈöªÊÅêÈæçÂ∞±Ë¶Å‰æÜÂí¨‰Ω†‰∫ÜÔºÅ</p>

    <!-- ËøîÂõûÈ¶ñÈ†ÅÊåâÈàï -->
    <router-link
      to="/"
      class="mt-6 px-6 py-3 bg-red-600 hover:bg-red-800 text-white font-bold rounded"
    >
      ÊªæÂõûÈ¶ñÈ†Å
    </router-link>

    <!-- ÊÅêÈæçÈÅäÊà≤ -->
    <div class="mt-8">
      <canvas ref="gameCanvas" class="border-2 border-white"></canvas>
    </div>
  </div>
</template>

<script setup>
import { onMounted, ref } from 'vue'

// Áç≤Âèñ Canvas DOM
const gameCanvas = ref(null)

onMounted(() => {
  if (!gameCanvas.value) return

  const canvas = gameCanvas.value
  const ctx = canvas.getContext('2d')

  // Ë®≠ÂÆöÁï´Â∏ÉÂ§ßÂ∞è
  canvas.width = 600
  canvas.height = 200

  // Âä†ËºâÂúñÁâá
  const dinoImg = new Image()
  dinoImg.src = new URL('@/assets/img/tyrannosaurus-rex.png', import.meta.url).href

  const obstacleImg = new Image()
  obstacleImg.src = new URL('@/assets/img/shortcuts/tree.png', import.meta.url).href

  // ÊÅêÈæçËÆäÊï∏
  let dino = { x: 50, y: 150, width: 50, height: 50, dy: 0, jumping: false }
  let obstacles = []
  let gravity = 0.5
  let gameSpeed = 4
  let gameOver = false

  // Áõ£ËÅΩÈçµÁõ§ÊåâÈçµ (Ë∑≥Ë∫ç)
  document.addEventListener('keydown', (e) => {
    if (e.code === 'Space' && !dino.jumping) {
      dino.dy = -10
      dino.jumping = true
    }
  })

  // Áî¢ÁîüÈöúÁ§ôÁâ©
  const spawnObstacle = () => {
    if (Math.random() < 0.02) {
      obstacles.push({ x: canvas.width, y: 150, width: 30, height: 50 })
    }
  }

  // ÈÅäÊà≤Êõ¥Êñ∞
  const updateGame = () => {
    if (gameOver) return

    ctx.clearRect(0, 0, canvas.width, canvas.height)

    // Êõ¥Êñ∞ÊÅêÈæç
    dino.y += dino.dy
    dino.dy += gravity

    if (dino.y > 150) {
      dino.y = 150
      dino.jumping = false
    }

    // Êõ¥Êñ∞ÈöúÁ§ôÁâ©
    spawnObstacle()
    for (let i = 0; i < obstacles.length; i++) {
      obstacles[i].x -= gameSpeed

      // Á¢∞ÊíûÊ™¢Ê∏¨
      if (
        dino.x < obstacles[i].x + obstacles[i].width &&
        dino.x + dino.width > obstacles[i].x &&
        dino.y < obstacles[i].y + obstacles[i].height &&
        dino.y + dino.height > obstacles[i].y
      ) {
        gameOver = true
        alert('ÈÅäÊà≤ÁµêÊùüÔºÅ‰Ω†ÁúüÁöÑÂæàÂª¢üòÜ')
        location.reload()
      }
    }

    // ÁßªÈô§Èõ¢ÈñãÁï´Èù¢ÁöÑÈöúÁ§ôÁâ©
    obstacles = obstacles.filter((obs) => obs.x > -obs.width)

    // Áï´ÊÅêÈæç
    ctx.drawImage(dinoImg, dino.x, dino.y, dino.width, dino.height)

    // Áï´ÈöúÁ§ôÁâ©
    for (let obs of obstacles) {
      ctx.drawImage(obstacleImg, obs.x, obs.y, obs.width, obs.height)
    }

    requestAnimationFrame(updateGame)
  }

  // ÈñãÂßãÈÅäÊà≤
  updateGame()
})
</script>

<style scoped>
/* ËÆì 404 Ê®ôÈ°åÈñÉÁàç */
@keyframes bounce {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}
.animate-bounce {
  animation: bounce 0.5s infinite;
}
</style>
