<template>
  <div>
    <div class="bg-white min-h-screen text-black px-6 sm:px-10">
      <!-- ‚úÖ Á¨¨‰∏ÄÂ±§ (ÈõªËÖ¶Áâà HeaderÔºåÊâãÊ©üÁâàÊîπËÆäÊéíÁâà) -->
      <section class="flex flex-col sm:flex-row items-center py-4 sm:py-8">
        <div class="flex justify-between items-center w-full sm:w-auto">
          <button class="sm:hidden" @click="showAlert">
            <img src="../../assets/img/grid.png" alt="" class="h-6 w-6" />
          </button>

          <!-- ‚úÖ ÊâãÊ©üÁâà Google Ê®ôË™å (Â±Ö‰∏≠) -->
          <router-link
            to="/"
            class="text-4xl font-bold tracking-wide text-center sm:text-left sm:text-3xl sm:mr-6"
          >
            Poopal
          </router-link>

          <button class="sm:hidden" @click="showAlert">
            <img src="../../assets/img/profile.png" alt="" class="h-6 w-6" />
          </button>
        </div>

        <!-- ‚úÖ ÊêúÂ∞ãÊ°Ü (ÊâãÊ©üÁâàÁΩÆ‰∏≠ÔºåÈõªËÖ¶ÁâàÈù†Â∑¶) -->
        <div
          class="flex items-center w-full sm:flex-grow sm:max-w-2xl bg-gray-200 rounded-full px-4 py-2 shadow-md focus-within:ring-2 focus-within:ring-gray-400 my-4 sm:my-0"
        >
          <!-- Ëº∏ÂÖ•Ê°Ü -->
          <input
            v-model="searchQuery"
            type="text"
            placeholder="ÊêúÂ∞ã Poopal ÊàñËº∏ÂÖ•Á∂≤ÂùÄ"
            class="flex-grow bg-transparent text-black outline-none text-base sm:text-lg"
          />

          <!-- ‚úÖ ÊåâÈàïÁæ§ÁµÑ (RWD: ËÆìË™ûÈü≥ & Lens Âè™Âú® `sm` ‰ª•‰∏äÈ°ØÁ§∫) -->
          <div class="flex items-center space-x-2 sm:space-x-3">
            <button class="text-gray-500 hover:text-black hidden sm:flex" @click="clearSearchQuery">
              ‚úñ
            </button>
            <button class="text-gray-500 hover:text-black hidden sm:flex" @click="showAlert">
              <img src="../../assets/img/voice-search.png" alt="voice search" class="h-5 w-5" />
            </button>
            <button class="text-gray-500 hover:text-black hidden sm:flex" @click="showAlert">
              <img src="../../assets/img/camera.png" alt="Google Lens" class="h-5 w-5" />
            </button>
            <button class="text-gray-500 hover:text-black" @click="executeSearch">
              <img src="../../assets/img/search.png" alt="search" class="h-5 w-5 sm:h-6 sm:w-6" />
            </button>
          </div>
        </div>

        <!-- ‚úÖ Âè≥ÂÅ¥ Google ÊáâÁî®Á®ãÂºè & ÂÄã‰∫∫Â∏≥Êà∂ (ÊâãÊ©üÁâàÁ∏ÆÂ∞èÈñìË∑ù) -->
        <div class="hidden md:flex items-center space-x-10 ml-auto">
          <button @click="showAlert">
            <img src="../../assets/img/grid.png" alt="" class="h-6 w-6" />
          </button>
          <button @click="showAlert">
            <img src="../../assets/img/profile.png" alt="" class="h-8 w-8 rounded-full" />
          </button>
        </div>
      </section>

      <!-- ‚úÖ Á¨¨‰∫åÂ±§Ôºöcategories ÈÅ∏ÂñÆ + ÊéíÂ∫èÈÅ∏ÂñÆ -->
      <div
        class="flex flex-col sm:flex-row items-center justify-between mt-2 sm:mt-0 text-sm text-gray-600"
      >
        <!-- ‚úÖ categories (ÂàÜÈ°ûÈÅ∏ÂñÆ) -->
        <nav class="overflow-x-auto whitespace-nowrap w-full sm:w-auto">
          <div class="flex space-x-6">
            <p
              v-for="(category, index) in categories"
              :key="index"
              @click="selectCategory(index)"
              :class="{
                'border-b-2 border-black text-black pb-1': selectedCategory === index,
                'hover:text-black cursor-pointer': true,
              }"
            >
              {{ category }}
            </p>
          </div>
        </nav>

        <!-- ‚úÖ ÊéíÂ∫èÊñπÂºè (ÊâãÊ©üÁâàÊèõË°åÔºåÈõªËÖ¶ÁâàÂ∞çÈΩäÂè≥ÂÅ¥) -->
        <div class="mt-2 sm:mt-0 flex items-center space-x-2 pb-1">
          <label for="filter" class="mr-2">ÊéíÂ∫èÊñπÂºèÔºö</label>
          <select
            id="filter"
            v-model="selectedFilter"
            class="border border-gray-300 rounded px-2 py-1 focus:ring focus:ring-gray-400"
          >
            <option v-for="(option, index) in filterOptions" :key="index" :value="option.value">
              {{ option.label }}
            </option>
          </select>
        </div>
      </div>

      <div class="my-8">
        <div class="space-y-4">
          <InfoCard v-for="(item, index) in paginatedResults" :key="index" :data="item" />
        </div>
      </div>
    </div>
    <!-- ÂàÜÈ†ÅÊåâÈàï -->
    <div class="flex flex-wrap justify-center items-center gap-2 sm:space-x-2 my-6">
      <!-- Á¨¨‰∏ÄÈ†Å -->
      <button
        @click="changePage(1)"
        :disabled="currentPage === 1"
        class="px-3 py-2 bg-gray-100 rounded hover:bg-gray-200 disabled:opacity-50 text-sm sm:text-base"
      >
        Á¨¨‰∏ÄÈ†Å
      </button>

      <!-- ‰∏ä‰∏ÄÈ†Å -->
      <button
        @click="changePage(currentPage - 1)"
        :disabled="currentPage === 1"
        class="px-3 py-2 bg-gray-100 rounded hover:bg-gray-200 disabled:opacity-50 text-sm sm:text-base"
      >
        ‰∏ä‰∏ÄÈ†Å
      </button>

      <!-- È°ØÁ§∫Áï∂ÂâçÈ†ÅÊï∏ -->
      <span class="px-4 py-2 bg-gray-200 rounded text-sm sm:text-base">
        Á¨¨ {{ currentPage }} È†Å / ÂÖ± {{ totalPages }} È†Å
      </span>

      <!-- ‰∏ã‰∏ÄÈ†Å -->
      <button
        @click="changePage(currentPage + 1)"
        :disabled="currentPage === totalPages"
        class="px-3 py-2 bg-gray-100 rounded hover:bg-gray-200 disabled:opacity-50 text-sm sm:text-base"
      >
        ‰∏ã‰∏ÄÈ†Å
      </button>

      <!-- ÊúÄÂæå‰∏ÄÈ†Å -->
      <button
        @click="changePage(totalPages)"
        :disabled="currentPage === totalPages"
        class="px-3 py-2 bg-gray-100 rounded hover:bg-gray-200 disabled:opacity-50 text-sm sm:text-base"
      >
        ÊúÄÂæå‰∏ÄÈ†Å
      </button>
    </div>
    <CameraModelBox :isOpen="isCameraModelOpen" @close="closeCameraModel" @open="openCamera" />
  </div>
</template>

<script setup>
import { ref, onMounted, computed, watch } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import InfoCard from '@/components/layouts/InfoCard.vue'

// ÂàÜÈ°ûÈÅ∏ÂñÆÈ†ÖÁõÆ
const categories = ref(['ÂÖ®ÈÉ®', 'Ë≥ºÁâ©', 'ÂúñÁâá', 'ÂΩ±Áâá', 'Êñ∞ËÅû', 'Á∂≤È†Å', 'Êõ∏Á±ç', 'Êõ¥Â§ö', 'Â∑•ÂÖ∑'])
const results = ref([])
const route = useRoute()
const searchQuery = ref('')

// Áï∂ÂâçÈ†ÅÈù¢ URL ÁöÑ query ÂèÉÊï∏
onMounted(() => {
  searchQuery.value = route.query.q || ''
})

// ËÆÄÂèñ JSON
onMounted(async () => {
  try {
    const res = await fetch('search_dataset.json') // public Ë≥áÊñôÂ§æÂÖßÁöÑÊ™îÊ°à
    if (!res.ok) {
      throw new Error('Failed to fetch data')
    }
    results.value = await res.json()
  } catch (err) {
    console.error('Error:', err)
  } finally {
    console.log('Data loaded')
  }
})

// Áï∂ÂâçÈÅ∏‰∏≠ÁöÑÂàÜÈ°ûÁ¥¢Âºï
const selectedCategory = ref(0)

// ÂàáÊèõÈÅ∏‰∏≠ÁöÑÂàÜÈ°û
const selectCategory = (index) => {
  selectedCategory.value = index
  currentPage.value = 1 // ÂàáÊèõÂàÜÈ°ûÊôÇÔºåÈáçÁΩÆÁï∂ÂâçÈ†ÅÊï∏
}

// ÊéíÂ∫èÈÅ∏È†Ö
const filterOptions = ref([
  { label: 'ÊúÄÂèóÊ≠°Ëøé', value: 'relevance' },
  { label: 'ÊúÄÊñ∞‰∏äÂÇ≥', value: 'latest' },
  { label: 'ÊúÄ‰∏çÂèóÊ≠°Ëøé', value: 'popularity' },
  { label: 'ÊúÄËàäÊñáÁ´†', value: 'oldest' },
  { label: 'È¶ñÂ≠óÊØçÊéíÂàó', value: 'alphabet' },
])

// Áï∂ÂâçÈÅ∏‰∏≠ÁöÑÁØ©ÈÅ∏Ê¢ù‰ª∂
const selectedFilter = ref('relevance')

watch(selectedFilter, () => {
  currentPage.value = 1 // ÂàáÊèõÊéíÂ∫èÊñπÂºèÊôÇÔºåÈáçÁΩÆÁï∂ÂâçÈ†ÅÊï∏
})

const filteredResults = computed(() => {
  return [...results.value].sort((a, b) => {
    if (selectedFilter.value === 'relevance') {
      return b.popularity - a.popularity // Áî±È´òÂà∞‰Ωé
    } else if (selectedFilter.value === 'latest') {
      return new Date(b.created_at) - new Date(a.created_at) // ÊúÄÊñ∞ÁöÑÊéíÂâçÈù¢
    } else if (selectedFilter.value === 'popularity') {
      return a.popularity - b.popularity // Áî±‰ΩéÂà∞È´ò
    } else if (selectedFilter.value === 'oldest') {
      return new Date(a.created_at) - new Date(b.created_at) // ÊúÄËàäÁöÑÊéíÂâçÈù¢
    } else if (selectedFilter.value === 'alphabet') {
      return a.title.localeCompare(b.title, 'zh-TW') // ‰∏≠ÊñáÊãºÈü≥ÊéíÂ∫è
    }
    return 0
  })
})

const currentPage = ref(1) // Áï∂ÂâçÈ†ÅÊï∏
const itemsPerPage = 10 // ÊØèÈ†ÅÈ°ØÁ§∫ 10 Á≠ÜË≥áÊñô

// Ë®àÁÆóÁ∏ΩÈ†ÅÊï∏
const totalPages = computed(() => Math.ceil(filteredResults.value.length / itemsPerPage))

// ÂèñÂæóÁï∂ÂâçÈ†ÅÈù¢Ë¶ÅÈ°ØÁ§∫ÁöÑË≥áÊñô
const paginatedResults = computed(() => {
  const start = (currentPage.value - 1) * itemsPerPage
  return filteredResults.value.slice(start, start + itemsPerPage)
})

// **üîπ ÂàáÊèõÈ†ÅÊï∏**
const changePage = (page) => {
  if (page >= 1 && page <= totalPages.value) {
    currentPage.value = page
    window.scrollTo({ top: 0, behavior: 'smooth' })
  }
}

const clearSearchQuery = () => {
  searchQuery.value = ''
}

const router = useRouter()

const executeSearch = () => {
  if (searchQuery.value.trim()) {
    // alert('fuck you')
    router.push({ path: '/result', query: { q: searchQuery.value.trim() } })
  }
}

const showAlert = () => {
  alert('This is a button with no function.')
}
</script>

<style scoped>
/* ÁßªÈô§ input Âú® iOS ÂÖßÂª∫ÁöÑÊ®£Âºè */
input::-webkit-search-decoration,
input::-webkit-search-cancel-button {
  display: none;
}

/* ËÆìÊªëÂãïÂçÄÂüüÁöÑ scrollbar ‰∏çÈ°ØÁ§∫ (ÈÅ©Áî®Êñº Chrome, Safari) */
nav::-webkit-scrollbar {
  display: none;
}
